define(function(require, exports, module) {
"use strict";

const autosuggest = require('antlr4-autosuggest');
const Range = require('ace/range').Range;

var Antlr4Completions = function(grammar) {
    this.autosuggester = autosuggest.autosuggester(grammar.Lexer, grammar.Parser);
    this.snippets = {};
    this.populateSnippets();
};

(function() {

    this.makeSnippet = function(caption, meta) {
        let i = 0;
        const q = meta == 'feature' || meta == 'scenario' ? '' : '"';
        const key = caption.replace(/[A-Z]{2}.*/, '');
        const snippet = {
            value: key,
            caption: caption,
            snippet: caption.replace(/[A-Z]{2,}/g, v => q + '${' + (++i) + ':' + v + '}' + q),
            meta: meta,
        };
        let sameSnippets = this.snippets[key];
        if (sameSnippets == null) sameSnippets = this.snippets[key] = [];
        sameSnippets.push(snippet);
    };

    this.populateSnippets = function() {
        this.makeSnippet('Feature: JIRA', 'feature');
        this.makeSnippet('Scenario: DESCRIPTION', 'scenario');
        // todo: calculate these dynamically from the ANTLR4 structure
        // for now, use extractSnippets to parse the grammar and produce this list
        this.makeSnippet('I wait for TIME TIME_UNIT', 'when');
        this.makeSnippet('I click on SELECTOR', 'when');
        this.makeSnippet('I double click on SELECTOR', 'when');
        this.makeSnippet('I right click on SELECTOR', 'when');
        this.makeSnippet('I hover on SELECTOR', 'when');
        this.makeSnippet('I focus on SELECTOR', 'when');
        this.makeSnippet('I tap on SELECTOR', 'when');
        this.makeSnippet('I set SELECTOR value to VALUE', 'when');
        this.makeSnippet('I execute scenario JIRA', 'when');
        this.makeSnippet('I open URL', 'when');
        this.makeSnippet('I run application APPLICATION ', 'when');
        this.makeSnippet('I switch to main window ', 'when');
        this.makeSnippet('I switch to window WINDOW_NAME', 'when');
        this.makeSnippet('I wait while SELECTOR appears', 'when');
        this.makeSnippet('I wait unless SELECTOR appears', 'when');
        this.makeSnippet('I scroll up ', 'when');
        this.makeSnippet('I scroll down ', 'when');
        this.makeSnippet('I scroll left ', 'when');
        this.makeSnippet('I scroll right ', 'when');
        this.makeSnippet('I press KEY', 'when');
        this.makeSnippet('I long press KEY', 'when');
        this.makeSnippet('I type KEYSTRING', 'when');
        this.makeSnippet('I drag ELEMENT and drop it into CONTAINER', 'when');
        this.makeSnippet('I handle captcha in SELECTOR', 'when');
        this.makeSnippet('I copy file from SOURCE_FILE to DESTINATION_FILE', 'when');
        this.makeSnippet('I move file from SOURCE_FILE to DESTINATION_FILE', 'when');
        this.makeSnippet('I delete file FILE', 'when');
        this.makeSnippet('I put a value from SELECTOR into VARIABLE', 'when');
        this.makeSnippet('I download FILEMASK', 'when');
        this.makeSnippet('I remove FILEMASK', 'when');
        this.makeSnippet('I list files in FOLDER folder', 'when');
        this.makeSnippet('I wait while FILEMASK appears', 'when');
        this.makeSnippet('I wait unless FILEMASK appears', 'when');
        this.makeSnippet('I put the list of files in FOLDER into VARIABLE', 'when');
        this.makeSnippet('I execute query QUERY', 'when');
        this.makeSnippet('I wait while QUERY returns empty set', 'when');
        this.makeSnippet('I wait unless QUERY returns empty set', 'when');
        this.makeSnippet('I put the result set into VARIABLE', 'when');
        this.makeSnippet('I execute command COMMAND', 'when');
        this.makeSnippet('I execute command COMMAND', 'when');
        this.makeSnippet('I execute script', 'when');
        this.makeSnippet('I wait while COMMAND succeeds', 'when');
        this.makeSnippet('I wait unless COMMAND succeeds', 'when');
        this.makeSnippet('I put the output into VARIABLE', 'when');
        this.makeSnippet('I set header HEADER to VALUE', 'when');
        this.makeSnippet('I set body to VALUE', 'when');
        this.makeSnippet('I set body to:', 'when');
        this.makeSnippet('I execute METHOD request for URL', 'when');
        this.makeSnippet('I put the response  into VARIABLE', 'when');
        this.makeSnippet('scenario steps should complete successfully', 'then');
        this.makeSnippet('PAGE page must be displayed', 'then');
        this.makeSnippet('I should see VALUE in SELECTOR', 'then');
        this.makeSnippet('I should see text in SELECTOR:', 'then');
        this.makeSnippet('I see window identified by SELECTOR as WINDOW_NAME', 'then');
        this.makeSnippet('I see simple notification TITLE ', 'then');
    };

    this.getCompletions = function(editor, session, pos, prefix, callback) {
        const priorText = session.getTextRange(new Range(0, 0, pos.row, pos.col));
        const completions = this.autosuggester.autosuggest(priorText);

        // use null token to indicate that it will matches anything
        if (completions.includes("\0")) {
            console.log("suggest: *");
            return;
        }

        const allCompletions = completions.map(k => {
            return this.snippets[k] || { caption: k.trim(), value: k };
        }).flat();

        console.log("suggest: ", allCompletions);
        callback(null, allCompletions);
    };

}).call(Antlr4Completions.prototype);

exports.Antlr4Completions = Antlr4Completions;

});
