define("ace/mode/antlr4", function(require, exports, module) {

var oop = require("ace/lib/oop");
var TextMode = require("ace/mode/text").Mode;
var WorkerClient = require("ace/worker/worker_client").WorkerClient;
var Antlr4Tokenizer = require("ace/ext/antlr4/tokenizer").Antlr4Tokenizer;
var Antlr4Completions = require("./antlr4_completions").Antlr4Completions;

var Mode = function(options) {
    this.$behaviour = this.$defaultBehaviour;

    if (!options.grammar) throw new Error('antlr4 grammar must be specified');
    this.grammar = options.grammar;

    this.$tokenizer = new Antlr4Tokenizer(this.grammar.Lexer, this.grammar.antlrTokenNameToAceTokenType);
    this.completer = new Antlr4Completions(this.grammar);
};
oop.inherits(Mode, TextMode);

(function() {
    this.$id = "ace/mode/antlr4";
    this.lineCommentStart = "#";

    this.createWorker = function(session) {
        var worker = new WorkerClient(["ace"], "ace/mode/antlr4_worker", "Antlr4Worker", "dist/worker-antlr4.js");
        worker.attachToDocument(session.getDocument());
        worker.on("errors", (e) => session.setAnnotations(e.data));
        worker.on("annotate", (e) => session.setAnnotations(e.data));
        worker.on("terminate", () => session.clearAnnotations());
        // todo: implement grammar dynamic load
        // this.send("importScripts", importScripts);
        // worker.send('setGrammar', [ 'dist/grammar-devfactory-e2e.js' ]);
        return worker;
    };

}).call(Mode.prototype);

exports.Mode = Mode;
});
